import React from 'react';
import { useAuth } from '../auth/AuthProvider';
import SEO from '../components/SEO';

const Dashboard: React.FC = () => {
  const { user } = useAuth();
  return (
    <div className="pt-8">
      <SEO title="Dashboard" description="Your student dashboard" />
      <section className="py-16 bg-white">
        <div className="max-w-4xl mx-auto px-4">
          <h1 className="text-3xl font-bold text-blue-900 mb-6">Welcome{user?.displayName ? `, ${user.displayName}` : ''}</h1>
          <div className="bg-gray-50 p-6 rounded-lg">
            <p className="text-gray-700">You are signed in with Google.</p>
            {user?.email && <p className="text-gray-600 mt-2">Email: {user.email}</p>}
          </div>
        </div>
      </section>
    </div>
  );
};

export default Dashboard;